@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,700&display=swap);
.map-root {
  height: 100vh;
  width: 100vw;
  position: relative;
  z-index: 0; }
  .map-root .leaflet-container {
    height: 100%;
    width: 100%;
    z-index: 0; }
  .map-root .incident-marker {
    background-color: #cc0202;
    border: 1px solid #cc0202;
    border-radius: 50%;
    opacity: 0.25; }

.menu-root {
  position: fixed;
  z-index: 1;
  box-shadow: 0 0 10px -2px rgba(0, 0, 0, 0.25);
  height: 100%;
  background-color: #424150;
  color: #fefffe;
  font-size: 0.9em;
  font-weight: 300;
  max-width: 350px; }
  .menu-root .menu-title,
  .menu-root .menu-content {
    padding: 10px; }
  .menu-root .menu-title {
    line-height: 1.7em;
    background-color: #33323c;
    padding: 20px 10px; }
    .menu-root .menu-title h1 {
      margin: 0; }
  .menu-root .menu-content {
    overflow-y: scroll; }
  .menu-root .menu-dates {
    margin-left: 1em;
    font-weight: 100;
    font-size: 0.8em; }
  .menu-root .menu-loader {
    padding: 1em 0;
    text-align: center; }
    .menu-root .menu-loader > div {
      margin: 0 auto; }
  .menu-root .menu-section {
    margin-bottom: 1em; }
  .menu-root .menu-section-title {
    text-transform: uppercase;
    margin: 0;
    margin-bottom: 4px;
    font-weight: 700;
    font-size: 1em;
    display: inline-block; }

.timeframe-picker .timeframe-picker__selector {
  width: 100%; }

.timeframe-picker .react-select__control {
  border: none !important;
  border-radius: 6px; }

.timeframe-picker .react-select__control,
.timeframe-picker .react-select__value-container,
.timeframe-picker .react-select__single-value,
.timeframe-picker .react-select__menu {
  background-color: #33323c;
  color: #fefffe; }

.timeframe-picker .react-select__menu {
  background-color: #33323c;
  color: white; }

.incident-group-picker {
  overflow: hidden;
  -webkit-transition: height 500ms ease-in-out;
  transition: height 500ms ease-in-out; }
  .incident-group-picker .group-checkbox {
    display: block;
    cursor: pointer;
    padding: 2px 5px;
    border-radius: 6px;
    position: relative; }
    .incident-group-picker .group-checkbox input {
      display: none; }
    .incident-group-picker .group-checkbox:hover {
      background-color: #33323c; }
    .incident-group-picker .group-checkbox.inactive .input-group__count,
    .incident-group-picker .group-checkbox.inactive .input-group__name {
      color: #626070; }
    .incident-group-picker .group-checkbox.inactive .input-group__name {
      text-decoration: line-through; }
  .incident-group-picker .input-group__circle {
    border-radius: 50%;
    display: inline-block;
    width: 8px;
    height: 8px;
    opacity: 0.75;
    position: relative;
    border-style: solid;
    border-width: 1px;
    margin-right: 0.5em; }
  .incident-group-picker .input-group__count {
    margin-left: 0.5em;
    color: #98ff98; }
  .incident-group-picker .incident-group-bulk-actions {
    display: flex; }
  .incident-group-picker .incident-group-bulk-action,
  .incident-group-picker .incident-group-expander {
    background-color: #33323c;
    text-align: center;
    padding: 7px;
    margin-top: 3px;
    border-radius: 6px;
    cursor: pointer; }
  .incident-group-picker .incident-group-bulk-action {
    flex-grow: 1; }
    .incident-group-picker .incident-group-bulk-action:not(:first-of-type) {
      margin-left: 3px; }

body {
  margin: 0; }

#app-root {
  font-family: 'Open Sans', sans-serif; }

